import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';

const Checkout = ({ cart, initCheckout }) => {
  const navigate = useNavigate();
  const [formData, setFormData] = useState({
    fullName: '',
    phone: '',
    email: '',
    address: '',
    city: '',
    pincode: ''
  });

  // Redirect to shop if cart is empty
  useEffect(() => {
    if (!cart || cart.length === 0) {
      navigate('/shop');
    }
  }, [cart, navigate]);

  const handleChange = (e) => {
    setFormData({...formData, [e.target.name]: e.target.value});
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    // 1. Generate Order ID and save details in App.js
    const order = initCheckout(formData);
    
    // 2. Go to Payment Page
    navigate('/payment');
  };

  if (!cart || cart.length === 0) return null;

  return (
    <div className="min-h-screen bg-gray-50 py-12 px-4">
      <div className="max-w-3xl mx-auto bg-white p-8 rounded-xl shadow-lg">
        <h1 className="text-3xl font-bold mb-6">Shipping Details</h1>
        
        <form onSubmit={handleSubmit} className="grid grid-cols-1 md:grid-cols-2 gap-6">
            <div className="md:col-span-2">
                <label className="block text-sm font-medium text-gray-700">Full Name</label>
                <input required name="fullName" onChange={handleChange} type="text" className="mt-1 block w-full px-3 py-2 border rounded-md" placeholder="John Doe" />
            </div>

            <div>
                <label className="block text-sm font-medium text-gray-700">Phone Number</label>
                <input required name="phone" onChange={handleChange} type="tel" className="mt-1 block w-full px-3 py-2 border rounded-md" placeholder="+91 9876543210" />
            </div>

            <div>
                <label className="block text-sm font-medium text-gray-700">Email Address</label>
                <input required name="email" onChange={handleChange} type="email" className="mt-1 block w-full px-3 py-2 border rounded-md" placeholder="john@example.com" />
            </div>

            <div className="md:col-span-2">
                <label className="block text-sm font-medium text-gray-700">Full Address (House No, Street)</label>
                <textarea required name="address" onChange={handleChange} rows="3" className="mt-1 block w-full px-3 py-2 border rounded-md" placeholder="Flat 101, Green Apartments, Main Road"></textarea>
            </div>

            <div>
                <label className="block text-sm font-medium text-gray-700">City</label>
                <input required name="city" onChange={handleChange} type="text" className="mt-1 block w-full px-3 py-2 border rounded-md" placeholder="Mumbai" />
            </div>

            <div>
                <label className="block text-sm font-medium text-gray-700">Pincode</label>
                <input required name="pincode" onChange={handleChange} type="text" className="mt-1 block w-full px-3 py-2 border rounded-md" placeholder="400001" />
            </div>

            <div className="md:col-span-2 mt-4">
                <button type="submit" className="w-full bg-black text-white py-3 rounded-lg font-bold hover:bg-gray-800 transition">
                    Continue to Payment
                </button>
            </div>
        </form>
      </div>
    </div>
  );
};

export default Checkout;