import React, { useState, useEffect } from 'react';
import { Package, Users, Plus, Edit, Trash2, CheckCircle, Truck, X, UploadCloud } from 'lucide-react';
import apiClient from '../api/apiClient';
import toast from 'react-hot-toast';

const AdminDashboard = () => {
  const [activeTab, setActiveTab] = useState('products');
  const [products, setProducts] = useState([]);
  const [orders, setOrders] = useState([]);
  const [loading, setLoading] = useState(true);
  
  // Modal State
  const [showModal, setShowModal] = useState(false);
  const [editingProduct, setEditingProduct] = useState(null);
  
  // Form State
  const [formData, setFormData] = useState({
    name: '',
    price: '',
    originalPrice: '',
    category: 'Roll-on',
    tag: '',
    description: '',
    isAvailable: true,
    image: ''
  });
  
  const [imageFile, setImageFile] = useState(null);
  const [imagePreview, setImagePreview] = useState(null);

  useEffect(() => {
    fetchData();
  }, [activeTab]);

  const fetchData = async () => {
    setLoading(true);
    try {
      if (activeTab === 'products') {
        const res = await apiClient.get('/products');
        setProducts(res.data);
      } else if (activeTab === 'orders') {
        const res = await apiClient.get('/orders/all-orders');
        setOrders(res.data);
      }
    } catch (error) {
      console.error("Error fetching admin data:", error);
      toast.error("Failed to load data");
    } finally {
      setLoading(false);
    }
  };

  const handleInputChange = (e) => {
    const { name, value, type, checked } = e.target;
    setFormData(prev => ({
        ...prev,
        [name]: type === 'checkbox' ? checked : value
    }));
  };

  const handleFileChange = (e) => {
      const file = e.target.files[0];
      if (file) {
          setImageFile(file);
          setImagePreview(URL.createObjectURL(file));
      }
  };

  const handleSubmit = async (e) => {
      e.preventDefault();
      
      const data = new FormData();
      data.append('name', formData.name);
      data.append('price', formData.price);
      data.append('originalPrice', formData.originalPrice);
      data.append('category', formData.category);
      data.append('tag', formData.tag);
      data.append('description', formData.description);
      data.append('isAvailable', formData.isAvailable);
      
      if (imageFile) {
          data.append('imageFile', imageFile);
      } else {
          data.append('image', formData.image);
      }

      try {
          if (editingProduct) {
              await apiClient.put(`/products/${editingProduct._id}`, data);
              toast.success("Product Updated!");
          } else {
              await apiClient.post('/products', data);
              toast.success("Product Created!");
          }
          setShowModal(false);
          setEditingProduct(null);
          setFormData({ name: '', price: '', originalPrice: '', category: 'Roll-on', tag: '', description: '', isAvailable: true, image: '' });
          setImageFile(null);
          setImagePreview(null);
          fetchData();
      } catch (error) {
          console.error(error);
          toast.error("Operation failed.");
      }
  };

  const openEditModal = (product) => {
      setEditingProduct(product);
      setFormData(product);
      setImagePreview(product.image);
      setImageFile(null);
      setShowModal(true);
  };

  const handleDelete = async (id) => {
      if(window.confirm("Are you sure?")) {
          try {
              await apiClient.delete(`/products/${id}`);
              toast.success("Product Deleted");
              fetchData();
          } catch (error) {
              toast.error("Delete failed");
          }
      }
  };

  const updateOrderStatus = async (orderId, newStatus) => {
    try {
        await apiClient.put(`/orders/${orderId}/status`, { status: newStatus });
        toast.success(`Order updated to ${newStatus}`);
        fetchData();
    } catch (error) {
        toast.error("Failed to update status");
    }
  };

  const getStatusColor = (status) => {
      if (status === 'Delivered') return 'text-green-600 bg-green-100';
      if (status === 'Shipped') return 'text-blue-600 bg-blue-100';
      return 'text-yellow-600 bg-yellow-100';
  };

  return (
    <div className="min-h-screen bg-gray-50 flex">
      <div className="w-64 bg-gray-900 text-white p-6 hidden md:block fixed h-full">
        <h2 className="text-2xl font-bold mb-8 text-green-400">Admin Panel</h2>
        <nav className="space-y-4">
          <button onClick={() => setActiveTab('products')} className={`flex items-center gap-3 w-full p-3 rounded-lg transition ${activeTab === 'products' ? 'bg-green-700' : 'hover:bg-gray-800'}`}>
            <Package size={20} /> Products
          </button>
          <button onClick={() => setActiveTab('orders')} className={`flex items-center gap-3 w-full p-3 rounded-lg transition ${activeTab === 'orders' ? 'bg-green-700' : 'hover:bg-gray-800'}`}>
            <Truck size={20} /> Orders
          </button>
        </nav>
      </div>

      <div className="flex-1 p-8 md:ml-64">
         <h1 className="text-3xl font-bold mb-8 capitalize">{activeTab} Management</h1>

         {loading ? <p>Loading...</p> : (
             <>
                {activeTab === 'products' && (
                    <div>
                        <button onClick={() => { setEditingProduct(null); setShowModal(true); setImagePreview(null); }} className="bg-green-700 text-white px-6 py-2 rounded-lg font-bold mb-6 flex items-center gap-2 hover:bg-green-800">
                            <Plus size={20}/> Add New Product
                        </button>

                        <div className="bg-white rounded-xl shadow-sm overflow-hidden">
                            <table className="w-full text-left">
                                <thead className="bg-gray-100 border-b">
                                    <tr>
                                        <th className="p-4">Image</th>
                                        <th className="p-4">Product Name</th>
                                        <th className="p-4">Price</th>
                                        <th className="p-4">Category</th>
                                        <th className="p-4">Actions</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {products.map(p => (
                                        <tr key={p._id} className="border-b hover:bg-gray-50">
                                            <td className="p-4">
                                                <div className="w-12 h-12 rounded bg-gray-100 overflow-hidden">
                                                    {p.image ? <img src={p.image} alt="" className="w-full h-full object-cover"/> : <Package className="w-full h-full p-2 text-gray-400"/>}
                                                </div>
                                            </td>
                                            <td className="p-4 font-bold">{p.name}</td>
                                            <td className="p-4">{p.price}</td>
                                            <td className="p-4"><span className="bg-gray-100 px-2 py-1 rounded text-xs">{p.category}</span></td>
                                            <td className="p-4 flex gap-3">
                                                <button onClick={() => openEditModal(p)} className="text-blue-600 hover:text-blue-800"><Edit size={18}/></button>
                                                <button onClick={() => handleDelete(p._id)} className="text-red-600 hover:text-red-800"><Trash2 size={18}/></button>
                                            </td>
                                        </tr>
                                    ))}
                                </tbody>
                            </table>
                        </div>
                    </div>
                )}

                {activeTab === 'orders' && (
                    <div className="bg-white rounded-xl shadow-sm overflow-hidden">
                        <table className="w-full text-left">
                            <thead className="bg-gray-100 border-b">
                                <tr>
                                    <th className="p-4">Order ID</th>
                                    <th className="p-4">Customer</th>
                                    <th className="p-4">Total</th>
                                    <th className="p-4">Status</th>
                                    <th className="p-4">Action</th>
                                </tr>
                            </thead>
                            <tbody>
                                {orders.map(order => (
                                    <tr key={order._id} className="border-b hover:bg-gray-50">
                                        <td className="p-4 font-mono text-sm text-gray-500">#{order._id.slice(-6)}</td>
                                        <td className="p-4">
                                            <div className="font-bold">{order.shippingAddress?.fullName || 'Guest'}</div>
                                            <div className="text-xs text-gray-400">{order.user?.email}</div>
                                        </td>
                                        <td className="p-4 font-bold">{order.totalPrice}</td>
                                        <td className="p-4">
                                            <span className={`px-3 py-1 rounded-full text-xs font-bold ${getStatusColor(order.status)}`}>
                                                {order.status || 'Pending'}
                                            </span>
                                        </td>
                                        <td className="p-4">
                                            <select 
                                                className="border rounded px-2 py-1 text-sm bg-white"
                                                value={order.status || 'Pending'}
                                                onChange={(e) => updateOrderStatus(order._id, e.target.value)}
                                            >
                                                <option value="Pending">Pending</option>
                                                <option value="Shipped">Shipped</option>
                                                <option value="Delivered">Delivered</option>
                                            </select>
                                        </td>
                                    </tr>
                                ))}
                            </tbody>
                        </table>
                    </div>
                )}
             </>
         )}
      </div>

      {/* --- BETTER MODAL (Sticky Header + Footer) --- */}
      {showModal && (
        <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50 p-4">
            <div className="bg-white rounded-2xl shadow-2xl w-full max-w-lg max-h-[90vh] flex flex-col">
                
                {/* Sticky Header */}
                <div className="p-6 border-b flex justify-between items-center sticky top-0 bg-white rounded-t-2xl z-10">
                    <h2 className="text-xl font-bold">{editingProduct ? 'Edit Product' : 'Add New Product'}</h2>
                    <button onClick={() => setShowModal(false)} className="p-2 hover:bg-gray-100 rounded-full"><X size={24}/></button>
                </div>

                {/* Scrollable Content */}
                <div className="p-6 overflow-y-auto flex-grow">
                    <form id="productForm" onSubmit={handleSubmit} className="space-y-4">
                        <div>
                            <label className="block text-sm font-bold mb-1">Product Name</label>
                            <input name="name" value={formData.name} onChange={handleInputChange} className="w-full border p-2 rounded-lg" required />
                        </div>
                        
                        <div className="grid grid-cols-2 gap-4">
                            <div>
                                <label className="block text-sm font-bold mb-1">Price ()</label>
                                <input name="price" type="number" value={formData.price} onChange={handleInputChange} className="w-full border p-2 rounded-lg" required />
                            </div>
                            <div>
                                <label className="block text-sm font-bold mb-1">Original Price</label>
                                <input name="originalPrice" type="number" value={formData.originalPrice} onChange={handleInputChange} className="w-full border p-2 rounded-lg" />
                            </div>
                        </div>

                        <div>
                            <label className="block text-sm font-bold mb-1">Category</label>
                            <select name="category" value={formData.category} onChange={handleInputChange} className="w-full border p-2 rounded-lg">
                                <option value="Roll-on">Roll-on</option>
                                <option value="Foot Spray">Foot Spray</option>
                                <option value="Intimate">Intimate</option>
                                <option value="Other">Other</option>
                            </select>
                        </div>

                        <div>
                            <label className="block text-sm font-bold mb-1">Tag (e.g. Sale, New)</label>
                            <input name="tag" value={formData.tag} onChange={handleInputChange} className="w-full border p-2 rounded-lg" />
                        </div>

                        <div>
                            <label className="block text-sm font-bold mb-1">Product Image</label>
                            {imagePreview && (
                                <div className="mb-2 w-full h-32 bg-gray-100 rounded-lg overflow-hidden flex items-center justify-center border">
                                    <img src={imagePreview} alt="Preview" className="h-full object-contain" />
                                </div>
                            )}
                            <label className="flex items-center justify-center w-full border-2 border-dashed border-gray-300 p-3 rounded-lg cursor-pointer hover:border-green-500 hover:bg-green-50 transition">
                                <div className="flex flex-col items-center">
                                    <UploadCloud className="text-gray-400 mb-1" />
                                    <span className="text-sm text-gray-500">Click to upload image</span>
                                </div>
                                <input type="file" accept="image/*" onChange={handleFileChange} className="hidden" />
                            </label>
                        </div>

                        <div>
                            <label className="block text-sm font-bold mb-1">Description</label>
                            <textarea name="description" value={formData.description} onChange={handleInputChange} className="w-full border p-2 rounded-lg h-20"></textarea>
                        </div>

                        <div className="flex items-center gap-2">
                            <input type="checkbox" name="isAvailable" checked={formData.isAvailable} onChange={handleInputChange} className="w-5 h-5 accent-green-700" />
                            <label>Available for Sale</label>
                        </div>
                    </form>
                </div>

                {/* Sticky Footer */}
                <div className="p-6 border-t bg-white rounded-b-2xl sticky bottom-0 z-10">
                    <button form="productForm" type="submit" className="w-full bg-green-700 text-white font-bold py-3 rounded-lg hover:bg-green-800 transition">
                        {editingProduct ? 'Update Product' : 'Create Product'}
                    </button>
                </div>
            </div>
        </div>
      )}
    </div>
  );
};

export default AdminDashboard;