import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import toast from 'react-hot-toast'; // Use toast instead of alert
import apiClient from '../api/apiClient'; // *** NEW: Import the global API client ***

const Payment = ({ order, clearCart, user }) => {
 const [method, setMethod] = useState('card');
 const [loading, setLoading] = useState(false);
 const navigate = useNavigate();

 // Redirect if order is missing
 if (!order || order.total === 0) {
  navigate('/');
  return null;
 }

 const handlePayment = async (e) => {
  e.preventDefault();
  setLoading(true);
  
  // Check if user is logged in (token check is handled by apiClient interceptor)
  if (!user) {
    toast.error("Please log in to finalize your order.");
    setLoading(false);
    navigate('/login');
    return;
  }

  try {
    // Prepare data for the backend API
    const orderData = {
      items: order.items.map(item => ({
        _id: item._id, // Send the MongoDB product ID
        name: item.name,
        price: item.price,
        quantity: item.quantity
      })),
      shippingAddress: order.details,
      totalPrice: order.total
    };

    // 1. Simulate Payment (Delay)
    toast.loading("Processing Payment...", { id: 'payment-status' });
    await new Promise(resolve => setTimeout(resolve, 2000));
    toast.dismiss('payment-status');
    
    // 2. Submit Order to Backend (Using apiClient.post)
    // apiClient automatically handles: 
        // - Base URL ('/api')
        // - Content-Type: application/json
        // - Authorization: Bearer {token} (and token refresh if needed)
    const res = await apiClient.post('/orders', orderData);

    if (res.status >= 400) {
            // Axios generally throws an error for 4xx/5xx status codes,
            // but this is an extra safety check for success status.
      throw new Error(res.data.message || 'Order submission failed');
    }

    // 3. Success
    toast.success("Payment Successful! Order Placed.");
    clearCart(); // Empty the cart
    navigate('/account'); // Go to the new user account page
    
  } catch (error) {
    console.error("Order Error:", error);
    // Display error message from the response data if available, otherwise general message
    const errorMessage = error.response?.data?.message || error.message || "Payment failed. Please try again.";
    toast.error(errorMessage);
  } finally {
    setLoading(false);
  }
 };

 return (
  <div className="min-h-screen bg-gray-50 py-12 px-4 sm:px-6 lg:px-8 flex justify-center items-center">
   <div className="max-w-md w-full bg-white p-8 rounded-xl shadow-lg">
    <h2 className="text-2xl font-bold mb-6 text-center">Checkout</h2>
    <div className="mb-6 text-center">
      <p className="text-gray-500">Total Amount to Pay</p>
      <p className="text-3xl font-bold text-green-600">Rs. {order.total}</p>
    </div>

    {/* Payment Method Tabs */}
    <div className="flex gap-4 mb-6">
      <button 
        onClick={() => setMethod('card')}
        className={`flex-1 py-2 rounded-lg border ${method === 'card' ? 'bg-black text-white border-black' : 'bg-white text-gray-600'}`}
      >
        Card
      </button>
      <button 
        onClick={() => setMethod('upi')}
        className={`flex-1 py-2 rounded-lg border ${method === 'upi' ? 'bg-black text-white border-black' : 'bg-white text-gray-600'}`}
      >
        UPI
      </button>
    </div>

    <form onSubmit={handlePayment} className="space-y-4">
      {method === 'card' ? (
        <>
          <input required type="text" placeholder="Card Number" className="w-full px-4 py-2 border rounded-md" maxLength="16" />
          <div className="flex gap-4">
            <input required type="text" placeholder="MM/YY" className="w-1/2 px-4 py-2 border rounded-md" />
            <input required type="password" placeholder="CVV" className="w-1/2 px-4 py-2 border rounded-md" maxLength="3" />
          </div>
          <input required type="text" placeholder="Card Holder Name" className="w-full px-4 py-2 border rounded-md" />
        </>
      ) : (
        <>
          <input required type="text" placeholder="Enter UPI ID (e.g. name@okhdfcbank)" className="w-full px-4 py-2 border rounded-md" />
          <p className="text-xs text-gray-500 text-center">A request will be sent to your UPI app.</p>
        </>
      )}
      
      <button 
        className="w-full bg-green-600 text-white py-3 rounded-md font-bold hover:bg-green-700 transition disabled:bg-gray-500"
        disabled={loading}
      >
        {loading ? 'Processing...' : `Pay Rs. ${order.total}`}
      </button>
    </form>
    
    <div className="mt-6 p-3 bg-gray-100 rounded-lg text-sm text-gray-600">
            <p className="font-semibold">Order Summary:</p>
            <p>Shipping to: {order.details.address}, {order.details.city}, {order.details.pincode}</p>
            <p>Contact: {order.details.phone}</p>
        </div>
   </div>
  </div>
 );
};

export default Payment;