import React, { useState, useEffect } from 'react';
import { useNavigate, Link } from 'react-router-dom';
import { ShoppingBag, MapPin, Loader, Clock, User, Heart, Star } from 'lucide-react';
import toast from 'react-hot-toast';
import apiClient from '../api/apiClient'; // *** NEW: Import the global API client ***
import axios from 'axios'; // For error checking

const PHITKU_GREEN = '#059669'; // Custom Brand Color

const UserProfile = ({ user }) => {
 const [orders, setOrders] = useState([]);
 const [loading, setLoading] = useState(true);
 const navigate = useNavigate();

 useEffect(() => {
  // Redirect if user is not logged in
  if (!user) {
   navigate('/login');
   return;
  }
  
  const fetchOrders = async () => {
   try {
        // *** SECURITY/ROBUSTNESS UPDATE: Using apiClient ***
        // apiClient automatically includes the Access Token and handles refresh logic.
    const res = await apiClient.get('/orders/myorders');

    // Axios returns data in res.data
    setOrders(res.data);
   } catch (err) {
    console.error("Order fetch error:", err);
        // Handle specific 401 error if token refresh fails (handled by apiClient interceptor retry)
        const errorMessage = axios.isAxiosError(err) && err.response?.data?.message || "Could not load order history. Session may be expired.";
    toast.error(errorMessage);
   } finally {
    setLoading(false);
   }
  };

  fetchOrders();
 }, [user, navigate]);

 if (!user) return null; // Wait for redirect

 return (
  <div className="min-h-screen bg-gray-50 py-12 px-4">
   <div className="max-w-5xl mx-auto">
    
    <h1 className="text-4xl font-bold mb-8">My Account</h1>

    {/* User Info Card (Aesthetic Update) */}
    <div className="bg-white p-8 rounded-2xl shadow-xl mb-10 border-l-8" style={{borderColor: PHITKU_GREEN}}>
      <h2 className="text-3xl font-bold mb-3 text-gray-900 flex items-center gap-3">
                <User size={28} style={{color: PHITKU_GREEN}} /> Welcome back, {user.name.split(' ')[0]}
            </h2>
      <div className="flex flex-wrap gap-x-12 gap-y-2 text-gray-600 text-sm">
        <p className="flex items-center gap-2"><MapPin size={18} /> {user.email}</p>
        <p className="flex items-center gap-2"><Clock size={18} /> User ID: {user.id.substring(0, 10)}...</p>
      </div>
      {user.isAdmin && (
       <Link to="/admin" className="mt-6 inline-block bg-purple-600 text-white px-5 py-2 rounded-lg font-bold hover:bg-purple-700 transition shadow-md">
        Go to Admin Dashboard
       </Link>
      )}
    </div>

    {/* Order History Section */}
    <h2 className="text-3xl font-bold mb-6 flex items-center gap-3" style={{color: PHITKU_GREEN}}>
      <ShoppingBag size={24}/> Order History
    </h2>

    {loading ? (
      <div className="flex justify-center py-10"><Loader className="animate-spin" size={32} style={{color: PHITKU_GREEN}} /></div>
    ) : orders.length === 0 ? (
      <div className="text-center py-10 bg-white rounded-xl shadow-md">
        <p className="text-gray-500 mb-4">You have no past orders.</p>
        <Link to="/shop" className="text-green-600 hover:underline mt-2 inline-block font-medium">Start Shopping</Link>
      </div>
    ) : (
      <div className="space-y-6">
        {orders.map((order) => (
          <div key={order._id} className="bg-white p-6 rounded-2xl shadow-lg border border-gray-100 transition hover:shadow-xl">
            <div className="flex justify-between items-start border-b pb-4 mb-4">
              <div>
                <p className="font-bold text-xl text-gray-800">Order ID: #{order._id.substring(0, 10).toUpperCase()}</p>
                <p className="text-sm text-gray-500 flex items-center gap-1 mt-1">
                                    <Clock size={14} /> Ordered on: {new Date(order.createdAt).toLocaleDateString()}
                                </p>
              </div>
              <span className={`px-3 py-1 text-sm font-bold rounded-full ${
                order.status === 'Delivered' ? 'bg-green-100 text-green-700' : 
                                order.status === 'Shipped' ? 'bg-blue-100 text-blue-700' : 'bg-yellow-100 text-yellow-700'
              }`}>
                {order.status || 'Processing'}
              </span>
            </div>
            
            {/* Order Items */}
            <div className="space-y-3 mb-4">
              <p className="text-xs font-semibold uppercase text-gray-500 mb-2">Items Purchased ({order.items.length})</p>
              {order.items.map((item) => (
                <div key={item._id} className="flex justify-between text-sm">
                  <span className="text-gray-700 font-medium">{item.name} x {item.quantity}</span>
                  <span className="font-semibold">Rs. {item.price * item.quantity}</span>
                </div>
              ))}
            </div>

            {/* Total and Shipping */}
            <div className="border-t pt-4">
              <p className="text-2xl font-extrabold flex justify-between text-gray-900">
                <span>Total Paid</span>
                <span>Rs. {order.totalPrice}</span>
              </p>
              <p className="text-xs text-gray-500 mt-3 flex items-center gap-1">
                                <MapPin size={14} /> Shipping to: {order.shippingAddress.address}, {order.shippingAddress.city} - {order.shippingAddress.pincode}
              </p>
            </div>
          </div>
        ))}
      </div>
    )}
   </div>
  </div>
 );
};

export default UserProfile;