import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { Trash2, ArrowRight, Minus, Plus } from 'lucide-react';
import toast from 'react-hot-toast'; 

const Cart = ({ cartItems, removeFromCart, updateQuantity }) => {
  const navigate = useNavigate();
  const [promoCode, setPromoCode] = useState('');
  const [discount, setDiscount] = useState(0);

  // Calculate Subtotal based on price * quantity
  const subtotal = cartItems.reduce((acc, item) => acc + (item.price * item.quantity), 0);
  const total = subtotal - discount;

  // Coupon Logic
  const handleApplyCoupon = () => {
    if (promoCode.toUpperCase() === 'PHITKU30') {
        const discountAmount = Math.round(subtotal * 0.30);
        setDiscount(discountAmount);
        toast.success(`Coupon Applied! You saved Rs. ${discountAmount}`);
    } else {
        toast.error("Invalid Coupon Code");
        setDiscount(0);
    }
  };

  return (
    <div className="container mx-auto px-4 py-16 min-h-screen">
      <h1 className="text-3xl font-bold mb-8">Your Shopping Cart</h1>
      
      {cartItems.length === 0 ? (
        <div className="text-center py-10 bg-gray-50 rounded-xl">
            <p className="text-gray-500 mb-6 text-lg">Your cart is currently empty.</p>
            <Link to="/shop" className="bg-black text-white px-8 py-3 rounded-full font-bold hover:bg-gray-800 transition">
                Start Shopping
            </Link>
        </div>
      ) : (
        <div className="grid md:grid-cols-3 gap-8">
            <div className="md:col-span-2 space-y-4">
                {cartItems.map((item) => (
                    <div key={item._id} className="flex flex-col sm:flex-row justify-between items-center bg-white p-4 rounded-xl border border-gray-100 shadow-sm">
                        <div className="flex items-center gap-4 w-full sm:w-auto">
                            <div className={`w-16 h-16 rounded-lg flex-shrink-0 ${item.imageColor || 'bg-gray-100'}`}></div>
                            <div>
                                <h3 className="font-bold">{item.name}</h3>
                                <p className="text-sm text-gray-500">Unit Price: Rs. {item.price}</p>
                            </div>
                        </div>

                        {/* Quantity Controls */}
                        <div className="flex items-center gap-4 mt-3 sm:mt-0">
                            <div className="flex items-center border border-gray-300 rounded-lg">
                                <button
                                    onClick={() => updateQuantity(item._id, item.quantity - 1)}
                                    className="p-2 text-gray-600 hover:bg-gray-100 rounded-l-lg"
                                    title="Decrease quantity"
                                >
                                    <Minus size={16} />
                                </button>
                                <span className="px-4 font-semibold text-gray-800">{item.quantity}</span>
                                <button
                                    onClick={() => updateQuantity(item._id, item.quantity + 1)}
                                    className="p-2 text-gray-600 hover:bg-gray-100 rounded-r-lg"
                                    title="Increase quantity"
                                >
                                    <Plus size={16} />
                                </button>
                            </div>

                            {/* Item Total Price */}
                            <span className="font-bold w-20 text-right">Rs. {item.price * item.quantity}</span>
                            
                            {/* Remove Button */}
                            <button 
                                onClick={() => removeFromCart(item._id)}
                                className="text-red-500 hover:bg-red-50 p-2 rounded-full transition flex items-center gap-1"
                                title="Remove item"
                            >
                                <Trash2 size={18} />
                            </button>
                        </div>
                    </div>
                ))}
            </div>
            
            <div className="bg-gray-50 p-6 rounded-xl h-fit">
                <h3 className="text-xl font-bold mb-4">Order Summary</h3>
                
                {/* Coupon Code Section */}
                <div className="flex gap-2 mb-6">
                    <input 
                        type="text" 
                        placeholder="Promo Code" 
                        className="flex-1 px-3 py-2 border rounded-lg text-sm uppercase"
                        value={promoCode}
                        onChange={(e) => setPromoCode(e.target.value)}
                    />
                    <button 
                        onClick={handleApplyCoupon}
                        className="bg-black text-white px-4 py-2 rounded-lg text-sm font-bold hover:bg-gray-800 transition"
                    >
                        Apply
                    </button>
                </div>

                <div className="space-y-2 mb-4 text-sm text-gray-600">
                    <div className="flex justify-between">
                        <span>Subtotal ({cartItems.reduce((acc, item) => acc + item.quantity, 0)} items)</span>
                        <span>Rs. {subtotal}</span>
                    </div>
                    {discount > 0 && (
                        <div className="flex justify-between text-green-600 font-bold">
                            <span>Discount (30%)</span>
                            <span>- Rs. {discount}</span>
                        </div>
                    )}
                </div>

                <div className="flex justify-between mb-6 font-bold text-lg border-t pt-4">
                    <span>Total</span>
                    <span>Rs. {total}</span>
                </div>
                
                <button 
                    onClick={() => navigate('/checkout')}
                    disabled={cartItems.length === 0}
                    className="w-full bg-black text-white py-3 rounded-lg font-bold hover:bg-gray-800 transition disabled:bg-gray-400 flex justify-center items-center gap-2"
                >
                    Proceed to Checkout <ArrowRight size={18}/>
                </button>
            </div>
        </div>
      )}
    </div>
  );
};

export default Cart;