import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { Mail, Lock, User, ArrowRight, Loader } from 'lucide-react';
import toast from 'react-hot-toast';
import apiClient from '../api/apiClient'; 

const Login = ({ onLogin }) => {
  const [isLogin, setIsLogin] = useState(true);
  const [loading, setLoading] = useState(false);
  const navigate = useNavigate();

  const [formData, setFormData] = useState({
    name: '',
    email: '',
    password: ''
  });

  const handleChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);

    const endpoint = isLogin ? '/auth/login' : '/auth/register';
    
    if (!formData.email || !formData.password || (!isLogin && !formData.name)) {
        toast.error("Please fill in all fields.");
        setLoading(false);
        return;
    }

    try {
      const { data } = await apiClient.post(endpoint, formData);
      toast.success(isLogin ? "Welcome back!" : "Account created successfully!");
      
      localStorage.setItem('token', data.accessToken);
      localStorage.setItem('user', JSON.stringify(data.user));

      if (onLogin) onLogin(data.user);
      navigate('/');

    } catch (error) {
      console.error(error);
      const message = error.response?.data?.message || "Something went wrong.";
      toast.error(message);
    } finally {
      setLoading(false);
    }
  };

  return (
    // FIX: Reduced padding (py-8 instead of py-12) for mobile screens
    <div className="min-h-screen flex items-center justify-center bg-gray-50 py-8 px-4 sm:px-6 lg:px-8">
      
      {/* FIX: Responsive padding (p-6 on mobile, p-10 on PC) and spacing */}
      <div className="max-w-md w-full space-y-6 md:space-y-8 bg-white p-6 md:p-10 rounded-2xl shadow-xl">
        
        {/* Header */}
        <div className="text-center">
          {/* FIX: Smaller text on mobile (text-2xl) */}
          <h2 className="mt-2 text-2xl md:text-3xl font-extrabold text-gray-900">
            {isLogin ? 'Welcome Back' : 'Join the Phitku Tribe'}
          </h2>
          <p className="mt-2 text-sm text-gray-600">
            {isLogin ? "Sign in to manage your orders" : "Create an account for exclusive offers"}
          </p>
        </div>

        {/* Form */}
        <form className="mt-8 space-y-4 md:space-y-6" onSubmit={handleSubmit}>
          <div className="rounded-md shadow-sm space-y-4">
            
            {!isLogin && (
              <div className="relative">
                <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                  <User className="h-5 w-5 text-gray-400" />
                </div>
                <input
                  name="name"
                  type="text"
                  required={!isLogin}
                  className="appearance-none rounded-lg relative block w-full pl-10 px-3 py-3 border border-gray-300 placeholder-gray-500 text-gray-900 focus:outline-none focus:ring-green-500 focus:border-green-500 sm:text-sm"
                  placeholder="Full Name"
                  value={formData.name}
                  onChange={handleChange}
                />
              </div>
            )}

            <div className="relative">
              <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                <Mail className="h-5 w-5 text-gray-400" />
              </div>
              <input
                name="email"
                type="email"
                required
                className="appearance-none rounded-lg relative block w-full pl-10 px-3 py-3 border border-gray-300 placeholder-gray-500 text-gray-900 focus:outline-none focus:ring-green-500 focus:border-green-500 sm:text-sm"
                placeholder="Email address"
                value={formData.email}
                onChange={handleChange}
              />
            </div>

            <div className="relative">
              <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                <Lock className="h-5 w-5 text-gray-400" />
              </div>
              <input
                name="password"
                type="password"
                required
                className="appearance-none rounded-lg relative block w-full pl-10 px-3 py-3 border border-gray-300 placeholder-gray-500 text-gray-900 focus:outline-none focus:ring-green-500 focus:border-green-500 sm:text-sm"
                placeholder="Password"
                value={formData.password}
                onChange={handleChange}
              />
            </div>
          </div>

          <div>
            <button
              type="submit"
              disabled={loading}
              className={`group relative w-full flex justify-center py-3 px-4 border border-transparent text-sm font-medium rounded-lg text-white ${
                loading ? 'bg-green-400 cursor-not-allowed' : 'bg-green-700 hover:bg-green-800'
              } transition duration-150 ease-in-out`}
            >
              {loading ? (
                <span className="flex items-center gap-2">
                  <Loader className="animate-spin h-5 w-5" /> Processing...
                </span>
              ) : (
                <span className="flex items-center gap-2">
                  {isLogin ? 'Sign In' : 'Create Account'} <ArrowRight className="h-4 w-4" />
                </span>
              )}
            </button>
          </div>
        </form>

        <div className="text-center mt-4">
          <p className="text-sm text-gray-600">
            {isLogin ? "Don't have an account? " : "Already have an account? "}
            <button
              onClick={() => {
                 setIsLogin(!isLogin);
                 setFormData({ name: '', email: '', password: '' });
              }}
              className="font-medium text-green-600 hover:text-green-500"
            >
              {isLogin ? "Sign up" : "Log in"}
            </button>
          </p>
        </div>
      </div>
    </div>
  );
};

export default Login;